export default {
	props: {
		scrollTop: {
			type: Number,
			value: 0
		}
	},
	data() {
		return {
			height: 900
		}
	},
	methods: {
		backTop() {
			console.log('回到顶部')
			this.$emit('huiDaoDingBu')
		}
	},
	computed: {
		isShow() {
			if(this.scrollTop > this.height) {
				return true
			}else {
				return false
			}
		}
	}
}
